import * as telemetry from "./telemetry";
import "./style.css";

telemetry.newVisitor("homam").then(console.log).catch(console.log);

type TwoDCoordinate = {
  x: number;
  y: number;
};
type LastTwoDCoordinateEvent =
  | {
      time: null;
    }
  | {
      time: number;
      screen: TwoDCoordinate;
      page: TwoDCoordinate;
      client: TwoDCoordinate;
      offset: TwoDCoordinate;
    };

type LastTouchEvent =
  | {
      time: null;
    }
  | {
      time: number;
      screen: TwoDCoordinate;
      page: TwoDCoordinate;
      client: TwoDCoordinate;
      radius: TwoDCoordinate;
      force: number;
      rotationAngle: number;
      identifier: number;
    };

const NOW = new Date().valueOf();

let mousedown: LastTwoDCoordinateEvent = {
  time: null
};

let touchStart: LastTouchEvent = {
  time: null
};

let touchMove: LastTouchEvent = {
  time: null
};

let touchEnd: LastTouchEvent = {
  time: null
};

let isTouchStarted = false;
let isMouseDown = false;
let touchMoveStats: null | {
  radius: {
    max: TwoDCoordinate;
    min: TwoDCoordinate;
  };
  force: {
    max: number;
    min: number;
  };
} = null;

const logElement = document.getElementById("logElement") as HTMLElement;
function log(msg: string) {
  logElement.innerHTML = msg;
}

function onTouchCompleted(
  touchStart: LastTouchEvent,
  touchMove: LastTouchEvent,
  touchEnd: LastTouchEvent
) {
  log(
    JSON.stringify(
      {
        touchStart,
        touchMove,
        touchEnd
      },
      null,
      2
    )
  );
}

document.addEventListener("mousedown", (ev) => {
  isMouseDown = true;
  mousedown = {
    time: ev.timeStamp || new Date().valueOf() - NOW,
    screen: {
      x: ev.screenX,
      y: ev.screenY
    },
    page: {
      x: ev.pageX,
      y: ev.pageY
    },
    offset: {
      x: ev.offsetX,
      y: ev.offsetY
    },
    client: {
      x: ev.clientX,
      y: ev.clientY
    }
  };
});

document.addEventListener("touchstart", (ev) => {
  isTouchStarted = true;
  touchMove = {
    time: null
  };
  touchEnd = {
    time: null
  };
  const touch0 = ev.touches[0];
  touchStart = {
    time: ev.timeStamp || new Date().valueOf() - NOW,
    screen: {
      x: touch0.screenX,
      y: touch0.screenY
    },
    page: {
      x: touch0.pageX,
      y: touch0.pageY
    },
    radius: {
      x: touch0.radiusX,
      y: touch0.radiusX
    },
    force: touch0.force,
    rotationAngle: touch0.rotationAngle,
    identifier: touch0.identifier,
    client: {
      x: touch0.clientX,
      y: touch0.clientY
    }
  };
});

document.addEventListener("touchmove", (ev) => {
  const touch0 = ev.touches[0];
  touchMove = {
    time: ev.timeStamp || new Date().valueOf() - NOW,
    screen: {
      x: touch0.screenX,
      y: touch0.screenY
    },
    page: {
      x: touch0.pageX,
      y: touch0.pageY
    },
    radius: {
      x: touch0.radiusX,
      y: touch0.radiusX
    },
    force: touch0.force,
    rotationAngle: touch0.rotationAngle,
    identifier: touch0.identifier,
    client: {
      x: touch0.clientX,
      y: touch0.clientY
    }
  };
});

document.addEventListener("touchend", (ev) => {
  const touch0 = ev.changedTouches[0];
  touchEnd = {
    time: ev.timeStamp || new Date().valueOf() - NOW,
    screen: {
      x: touch0.screenX,
      y: touch0.screenY
    },
    page: {
      x: touch0.pageX,
      y: touch0.pageY
    },
    radius: {
      x: touch0.radiusX,
      y: touch0.radiusX
    },
    force: touch0.force,
    rotationAngle: touch0.rotationAngle,
    identifier: touch0.identifier,
    client: {
      x: touch0.clientX,
      y: touch0.clientY
    }
  };
  isTouchStarted = false;
  onTouchCompleted(touchStart, touchMove, touchEnd);
});

